<template>
  <!-- <div
    class="loading d-flex justify-content-center align-items-center"
    v-if="isFetch"
  >
    <img src="../../assets/Spinner2.gif" alt="spinner" />
  </div> -->

  <div class="page">
    <div class="content px-5">
      <h2 class="mb-5">Record Your Day</h2>
      <div class="row justify-content-around mt-5">
        <div class="card-content col-11 col-lg-5 py-5 px-5">
          <div class="mb-3">
            <label for="customRange1" class="form-label pe-3"
              >Start Time (24h)</label
            >
            <input type="time" class="input-time" v-model="start_time" />
          </div>
          <div class="mb-3 d-flex align-items-center justify-content-start">
            <label for="customRange1" class="form-label pe-3">Duration</label>
            <div class="w-75">
              <h4>{{ duration }} mins</h4>
              <input
                type="range"
                class="form-range"
                id="customRange1"
                max="120"
                v-model="duration"
              />
            </div>
          </div>
          <div class="mb-3">
            <label for="exampleFormControlTextarea1" class="form-label"
              >Content</label
            >
            <textarea
              class="form-control"
              id="exampleFormControlTextarea1"
              rows="3"
              maxlength="30"
              placeholder="eg. 'bench press 50kg * 10 * 4', 'rock climbing'"
              v-model="content"
            ></textarea>
          </div>

          <button type="button" class="btn btn-primary" @click="sumbit_record">
            Submit Record
          </button>
          <button type="button" class="btn btn-warning ms-3" @click="reset">
            Reset
          </button>
        </div>

        <div
          class="card-content records col-11 col-lg-5 mt-5 mt-lg-0 py-4 px-3"
        >
          <div class="d-flex align-items-center">
            <img class="today-img" src="../../assets/boxing.png" alt="" />
            <h3>{{ dateToday }}(Today)</h3>
          </div>

          <div
            class="alert alert-dark d-flex justify-content-between mb-2"
            role="alert"
            v-for="record in today"
            :key="record.record_id"
          >
            <div>
              <p class="mb-0">Start Time: {{ record.start_time }}</p>
              <p class="mb-0">Duration: {{ record.duration }}mins</p>
              <p class="mb-0">{{ record.description }}</p>
            </div>

            <button
              type="button"
              class="btn-close"
              aria-label="Close"
              @click="deleteRecord(record.record_id)"
            ></button>
          </div>
        </div>
      </div>

      <div class="d-flex justify-content-center">
        <div class="card-content col-11 justify-content-center row mt-5">
          <h3 class="mt-3 text-success">Last Three Days...</h3>
          <div class="card-content records col-lg-4 mt-5 mt-lg-0 py-4 px-3">
            <h3>{{ day1Tag }}</h3>
            <div v-if="day1.length === 0" class="text-center">
              <h3 class="mb-0 mt-4 text-warning">No record</h3>
              <h3 class="text-warning">...</h3>
            </div>
            <div
              v-else
              class="alert alert-dark d-flex justify-content-between mb-2"
              role="alert"
              v-for="record in day1"
              :key="record.record_id"
            >
              <div>
                <p class="mb-0">Start Time: {{ record.start_time }}</p>
                <p class="mb-0">Duration: {{ record.duration }}mins</p>
                <p class="mb-0">{{ record.description }}</p>
              </div>
            </div>
          </div>

          <div class="card-content records col-lg-4 mt-5 mt-lg-0 py-4 px-3">
            <h3>{{ day2Tag }}</h3>
            <div v-if="day2.length === 0">
              <h3 class="mb-0 mt-4 text-warning">No record</h3>
              <h3 class="text-warning">...</h3>
            </div>
            <div
              v-else
              class="alert alert-dark d-flex justify-content-between mb-2"
              role="alert"
              v-for="record in day2"
              :key="record.record_id"
            >
              <div>
                <p class="mb-0">Start Time: {{ record.start_time }}</p>
                <p class="mb-0">Duration: {{ record.duration }}mins</p>
                <p class="mb-0">{{ record.description }}</p>
              </div>
            </div>
          </div>

          <div class="card-content records col-lg-4 mt-5 mt-lg-0 py-4 px-3">
            <h3>{{ day3Tag }}</h3>
            <div v-if="day3.length === 0">
              <h3 class="mb-0 mt-4 text-warning">No record</h3>
              <h3 class="text-warning">...</h3>
            </div>
            <div
              v-else
              class="alert alert-dark d-flex justify-content-between mb-2"
              role="alert"
              v-for="record in day3"
              :key="record.record_id"
            >
              <div>
                <p class="mb-0">Start Time: {{ record.start_time }}</p>
                <p class="mb-0">Duration: {{ record.duration }}mins</p>
                <p class="mb-0">{{ record.description }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <img src="../../assets/resized.jpeg" alt="" class="background" />
  </div>
</template>

<script>
export default {
  name: 'HomePage',
  data() {
    return {
      start_time: '00:00',
      duration: '60',
      content: '',
      day1: null,
      day2: null,
      day3: null,
    };
  },
  created() {
    this.day1 = this.$store.getters.record1;
    this.day2 = this.$store.getters.record2;
    this.day3 = this.$store.getters.record3;
  },
  computed: {
    today() {
      return this.$store.getters.record_today;
    },
    dateToday() {
      return new Date().toDateString();
    },
    day1Tag() {
      return new Date(
        new Date().getTime() - 24 * 60 * 60 * 1000
      ).toDateString();
    },
    day2Tag() {
      return new Date(
        new Date().getTime() - 24 * 60 * 60 * 1000 * 2
      ).toDateString();
    },
    day3Tag() {
      return new Date(
        new Date().getTime() - 24 * 60 * 60 * 1000 * 3
      ).toDateString();
    },
  },
  methods: {
    async sumbit_record() {
      console.log(
        new Date(new Date().getTime() - 24 * 60 * 60 * 1000).toDateString()
      );
      console.log(this.start_time);
      console.log(this.duration);

      const newRecord = {
        description: this.content,
        user_id: this.$store.getters.user_id,
        date: new Date().toDateString(),
        start_time: this.start_time,
        duration: this.duration,
      };

      const response = await fetch('https://my-aware2.onrender.com/record', {
        method: 'POST',
        body: JSON.stringify(newRecord),
        headers: { 'content-type': 'application/json' },
      });

      if (!response.ok) {
        console.log('gege');
        return;
      }

      const responseData = await response.json();
      console.log(responseData);

      const response_record = await fetch(
        'https://my-aware2.onrender.com/record/search',
        {
          method: 'POST',
          body: JSON.stringify({
            date: new Date().toDateString(),
            user_id: this.$store.getters.user_id,
          }),
          headers: { 'content-type': 'application/json' },
        }
      );

      const responseDateRecord = await response_record.json();

      this.$store.commit('setToday', responseDateRecord);
    },
    reset() {
      this.duration = '60';
      this.start_time = '00:00';
      this.content = '';
    },
    async deleteRecord(record_id) {
      console.log(record_id);

      const deleteData = {
        record_id: record_id,
        user_id: this.$store.getters.user_id,
      };

      const response = await fetch('https://my-aware2.onrender.com/record', {
        method: 'DELETE',
        body: JSON.stringify(deleteData),
        headers: { 'content-type': 'application/json' },
      });

      if (!response.ok) {
        console.log('gege');
        return;
      }

      const response_record = await fetch(
        'https://my-aware2.onrender.com/record/search',
        {
          method: 'POST',
          body: JSON.stringify({
            date: new Date().toDateString(),
            user_id: this.$store.getters.user_id,
          }),
          headers: { 'content-type': 'application/json' },
        }
      );

      const responseDateRecord = await response_record.json();

      this.$store.commit('setToday', responseDateRecord);
    },
  },
};
</script>

