<template>
  <teleport to="body">
    <div v-if="show" @click="quit" class="backdrop"></div>
    <transition name="dialog">
      <dialog open v-if="show">
        <div class="position-relative">
          <img v-if="isLoading" src="../../assets/spinner_trans.gif" alt="spinner" class="position-absolute" style="top:45%;left: 40%;z-index: 100;"/>

          <header class="text-center">
          <img src="../../assets/boxing.png" alt="" />
          <h2 class="pt-3">Join Aware Family</h2>
          <div class="check1" v-if="httpCheck">Wrong Username or Password</div>
          <div class="check2" v-if="passwordSameCheck">Password and confirmPassword are not same</div>
        </header>
        <section>
          <div class="form-floating mb-4">
            <input
              type="text"
              class="form-control"
              :class="{ 'is-invalid': isUsernameNull }"
              id="floatingInput"
              placeholder="username"
              v-model="username"
            />
            <label for="floatingInput">Username</label>
            <div class="invalid-feedback">Please input the username.</div>
          </div>
          <div class="form-floating mb-4">
            <input
              type="text"
              class="form-control"
              :class="{ 'is-invalid': isFirstnameNull }"
              id="floatingInput"
              placeholder="firstname"
              v-model="firstname"
            />
            <label for="floatingInput">Firstname</label>
            <div class="invalid-feedback">Please input your firstname.</div>
          </div>
          <div class="form-floating mb-4">
            <input
              type="password"
              class="form-control"
              :class="{ 'is-invalid': isPasswordNull }"
              id="floatingPassword"
              placeholder="Password"
              v-model="password"
            />
            <label for="floatingPassword">Password</label>
            <div class="invalid-feedback">Please input the password.</div>
          </div>
          <div class="form-floating">
            <input
              type="password"
              class="form-control"
              :class="{ 'is-invalid': isConfirmPasswordNull }"
              id="floatingPassword"
              placeholder="confirmPassword"
              v-model="confirmPassword"
            />
            <label for="floatingPassword">Confirm Password</label>
            <div class="invalid-feedback">Please confirm your password.</div>
          </div>
        </section>
        <div class="text-center">
          <div>
            <button
              type="button"
              class="btn btn-primary py-2 my-2"
              @click="signup"
            >
              Signup
            </button>
          </div>
          <div>
            <button
              type="button"
              class="btn btn-warning py-2 my-2"
              @click="quit"
            >
              Cancel
            </button>
          </div>

          <h3>or</h3>
          <button
            type="button"
            class="btn btn-success py-2 mt-2 mb-4"
            @click="
              () => {
                this.$emit('toggle', 'signup');
              }
            "
          >
            Go to Login
          </button>
        </div>
        </div>
        
      </dialog>
    </transition>
  </teleport>
</template>

<script>
export default {
  props: {
    show: {
      type: Boolean,
      required: true,
    }
  },
  emits: ['close'],
  data() {
    return {
      username: '',
      password: '',
      firstname: '',
      confirmPassword: '',
      isUsernameNull: false,
      isPasswordNull: false,
      isFirstnameNull: false,
      isConfirmPasswordNull: false,
      httpCheck: false,
      passwordSameCheck:false,
      isLoading:false
    };
  },
  watch: {
    username(newVal) {
      if (newVal !== '') {
        this.isUsernameNull = false;
      }
    },
    password(newVal) {
      if (newVal !== '') {
        this.isPasswordNull = false;
      }
    },
    firstname(newVal) {
      if (newVal !== '') {
        this.isFirstnameNull = false;
      }
    },
    confirmPassword(newVal) {
      if (newVal !== '') {
        this.isConfirmPasswordNull = false;
      }
    },
  },
  computed: {},
  methods: {
    tryClose() {
      this.$emit('close');
    },
    async signup() {
      if (
        this.username === '' ||
        this.password === '' ||
        this.firstname === '' ||
        this.confirmPassword === ''
      ) {
        if (this.username === '') {
          this.isUsernameNull = true;
        }
        if (this.password === '') {
          this.isPasswordNull = true;
        }
        if (this.firstname === '') {
          this.isFirstnameNull = true;
        }
        if (this.confirmPassword === '') {
          this.isConfirmPasswordNull = true;
        }
        return;
      }else if(this.password !== this.confirmPassword){
        this.passwordSameCheck = true;
        return;
      }

      this.isLoading = true

      const signupData = {
        username :this.username,
        password : this.password,
        firstname : this.firstname
      }

      const response = await fetch('https://my-aware2.onrender.com/signup',{
        method: 'POST',
        body: JSON.stringify(signupData),
        headers: { 'content-type': 'application/json' },
      })

      if (!response.ok) {
        console.log('gege');
        return;
      }

      const responseData = await response.json()

      this.$store.commit('setUserId', responseData.user_id);
      this.$store.commit('setToken', responseData.token);
      this.$store.commit('setFirstname', responseData.firstname);
      this.$store.commit('setUsername', responseData.username);

      this.isLoading= false;

      this.$router.push('/home/inforecom');

    },
    quit() {
      this.$emit('quit', 'signup');
    },
  },
};
</script>

