import { createStore } from 'vuex';

export default createStore({
  state: {
    user_id: -1,
    firstname: '',
    username: '',
    token: '',
    record_today: [],
    record1: [],
    record2: [],
    record3: [],
  },
  getters: {
    user_id(state) {
      return state.user_id;
    },
    firstname(state) {
      return state.firstname;
    },
    username(state) {
      return state.username;
    },
    token(state) {
      return state.token;
    },
    record_today(state) {
      return state.record_today;
    },
    record1(state) {
      return state.record1;
    },
    record2(state) {
      return state.record2;
    },
    record3(state) {
      return state.record3;
    },
  },
  mutations: {
    setUsername(state, username) {
      state.username = username;
    },
    setUserId(state, user_id) {
      state.user_id = user_id;
    },
    setFirstname(state, firstname) {
      state.firstname = firstname;
    },
    setToken(state, token) {
      state.token = token;
    },
    setToday(state, record_today) {
      // record_today.forEach((element) => {
      //   state.record_today.push(element);
      // });
      state.record_today = record_today
    },
    set1(state, record1) {
      state.record1 = record1
    },
    set2(state, record2) {
      state.record2 = record2
    },
    set3(state, record3) {
      state.record3 = record3
    },
  },
  actions: {},
  modules: {},
});
