<template>
  <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
    <div class="container-fluid">
      <a class="navbar-brand" href="#"><img src="../../assets/logo.png" alt=""></a>
      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarTogglerDemo03"
        aria-controls="navbarTogglerDemo03"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      
      <div class="collapse navbar-collapse" id="navbarTogglerDemo03" style="flex-grow:0">
        <ul class="navbar-nav me-auto mb-2 mb-lg-0">
          <li class="nav-item px-2" @click="jump('inforecom')">
            <a class="nav-link" :class="{'active':now === 'inforecom'}" aria-current="page" href="#">Home</a>
          </li>
          <li class="nav-item px-2" @click="jump('myrecord')">
            <a class="nav-link" :class="{'active':now === 'myrecord'}" href="#">Record</a>
          </li>
          <li class="nav-item px-2" @click="jump('myinfo')">
            <a class="nav-link" :class="{'active':now === 'myinfo'}" href="#">MyInfo</a>
          </li>
          <li class="nav-item dropdown-center">
          <a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            Menu
          </a>
          <ul class="dropdown-menu dropdown-menu-dark" style="left:-65px;min-width: 200px;">
            <li class="mt-3"> <h4 class="text-center"> Signed in as </h4></li>
            <li> <h2 class="text-center"> {{username}} </h2> <hr> </li>
            <li><a class="dropdown-item text-center" href="#" @click="logout"><h3>Sign out</h3> </a></li>
          </ul>
        </li>
        </ul>

      </div>
    </div>
  </nav>
</template>

<script>
export default {
  data(){
    return {
      now:'inforecom'
    }
  },
  computed:{
    username(){
      return this.$store.getters.username
    }
  },
  methods:{
    jump(des){
      if(des === this.now){
        return;
      }else if(des === 'myinfo'){
        this.now = 'myinfo'
        this.$router.push('/home/myinfo')
      }else if(des === 'myrecord'){
        this.now = 'myrecord'
        this.$router.push('/home/myrecord')
      }else{
        this.now = 'inforecom'
        this.$router.push('/home/inforecom')
      }
    },
    logout(){
      this.$router.push('/')
    }
  }
};
</script>

<style scoped>
a{
  font-size: 2rem;
}

a img{
  width: 4rem;
}
</style>
