import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import HomePage from '../views/HomePage.vue'
import InfoRecom from '../components/Home/InfoRecom.vue'
import MyInfo from '../components/Home/MyInfo.vue'
import MyRecord from '../components/Home/MyRecord.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/home',
    name: 'homepage',
    component: HomePage,
    children:[
      {path:'inforecom',component:InfoRecom},
      {path:'myInfo',component:MyInfo},
      {path:'MyRecord',component:MyRecord}
    ]
  },

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
