<template>
  <div
    class="loading d-flex justify-content-center align-items-center"
    v-if="isFetch"
  >
    <img src="../../assets/Spinner2.gif" alt="spinner" />
  </div>

  <div class="page" v-if="!isFetch">
    <div class="content px-5">
      <div>
        <h1 class="mb-5">Hi {{ firstName }}</h1>
        
          <h2>{{ greetingMes }}
            <img v-if="icon_link" src="../../assets/half-moon.png" alt="" class="greet_img"/>
          <img v-else src="../../assets/morning.png" alt="" class="greet_img"/></h2>
          
        

        <h2>You are now in {{ area }}, {{ country }}</h2>
        <div class="row justify-content-around mt-5">
          <div class="card-content col-md-5 py-4">
            <div
              class="d-flex align-items-center text-center justify-content-center"
            >
              <img :src="weather_icon" alt="weather_icon" class="mb-2" />
            </div>
            <h3 class="text-center">{{ description }}</h3>
            <div class="d-flex justify-content-center alien-items-center">
              <h4 style="padding-top: 15px">temperature: &nbsp;</h4>
              <span>{{ temperature }}</span>
            </div>

            <div class="d-flex justify-content-center alien-items-center">
              <h4 style="padding-top: 15px">feels like: &nbsp;</h4>
              <span>{{ temperature_feel }}</span>
            </div>

            <div class="d-flex justify-content-center alien-items-center">
              <h4 style="padding-top: 15px">humidity: &nbsp;</h4>
              <span>{{ humidity }}</span>
            </div>

            <div class="d-flex justify-content-center alien-items-center">
              <h4 style="padding-top: 15px">wind speed: &nbsp;</h4>
              <span>{{ windspeed }}</span>
            </div>
          </div>

          <div class="card-content col-md-5 mt-md-0 mt-5 text-center py-4">
            <div
              v-if="isOutside"
              class="d-flex flex-column justify-content-evenly h-100"
            >
              <h2 class="text-success">Good Weather for Exercise</h2>
              <h3>Don't forget to bring your water cup before working out.</h3>
              <h3>Try doing these exercises</h3>

              <ul class="social">
                <li @mouseover="selectIndoor('ride bike')">
                  <a href="#"><img src="../../assets/bicycle.png" alt="" /></a>
                </li>
                <li @mouseover="selectIndoor('play football')">
                  <a href="#"
                    ><img src="../../assets/football-player.png" alt=""
                  /></a>
                </li>
                <li @mouseover="selectIndoor('play rugby')">
                  <a href="#"><img src="../../assets/rugby.png" alt="" /></a>
                </li>
                <li @mouseover="selectIndoor('rock climbing')">
                  <a href="#"><img src="../../assets/climbing.png" alt="" /></a>
                </li>
              </ul>
              <h3 style="color: rgba(54, 147, 224)">{{ indoorHovered }}</h3>
            </div>

            <div v-else class="d-flex flex-column justify-content-evenly h-100">
              <h2 class="text-secondary">Bad weather right?</h2>
              <h3>
                Don't worry! Gym would be a good place to exercise at any time!
              </h3>
              <h3>Try doing these exercises indoor</h3>

              <ul class="social">
                <li @mouseover="selectIndoor('body building')">
                  <a href="#"
                    ><img src="../../assets/weight-lifting.png" alt=""
                  /></a>
                </li>
                <li @mouseover="selectIndoor('yoga')">
                  <a href="#"><img src="../../assets/yoga.png" alt="" /></a>
                </li>
                <li @mouseover="selectIndoor('rope skipping')">
                  <a href="#"
                    ><img src="../../assets/skip-rope.png" alt=""
                  /></a>
                </li>
                <li @mouseover="selectIndoor('swimming')">
                  <a href="#"><img src="../../assets/swimming.png" alt="" /></a>
                </li>
              </ul>
              <h3 style="color: rgba(54, 147, 224)">{{ indoorHovered }}</h3>
            </div>
          </div>
        </div>
      </div>
    </div>
    <img src="../../assets/resized.jpeg" alt="" class="background" />
  </div>
</template>

<script>
export default {
  name: 'HomePage',
  data() {
    return {
      area: '',
      country: '',
      temperature: '',
      weather: '',
      sunset: null,
      sunrise: null,
      day: null,
      hour: '',
      temperature_feel: '',
      humidity: '',
      windspeed: '',
      weather_icon: '',
      description: '',
      isFetch: true,
      firstName: '',
      indoorHovered: '...',
    };
  },
  created() {
    const getWeather = async () => {
      const getPosition = function () {
        return new Promise((resolve, reject) => {
          navigator.geolocation.getCurrentPosition(resolve, reject);
        });
      };

      const location = await getPosition().then((position) => {
        console.log(position.coords);
        this.latitude = position.coords.latitude;
        this.longitude = position.coords.longitude;
        return [this.latitude, this.longitude];
      });

      const response = await fetch(
        `https://api.openweathermap.org/data/2.5/weather?lat=${location[0]}&lon=${location[1]}&appid=14aa8751f26b21e4c54e2768274bbb84`
      );

      const responseData = await response.json();

      console.log(responseData);

      // const response2 = await fetch(
      //   `https://api.openweathermap.org/data/2.5/forecast?lat=${location[0]}&lon=${location[1]}&appid=14aa8751f26b21e4c54e2768274bbb84`
      // );

      // const responseData2 = await response2.json();

      // console.log("haha");

      // console.log(responseData2);

      const secondsOfSunrise = responseData.sys.sunrise;
      const secondsOfSunset = responseData.sys.sunset;
      const secondsOfDay = responseData.dt;

      this.day = new Date(secondsOfDay * 1000).toLocaleDateString();
      this.sunset = new Date(secondsOfSunset * 1000).toLocaleTimeString();
      this.sunrise = new Date(secondsOfSunrise * 1000).toLocaleTimeString();
      this.hour = new Date(secondsOfDay * 1000).getHours();

      // console.log(this.day, this.sunset, this.sunrise, this.hour);

      this.area = responseData.name;
      this.country = responseData.sys.country;
      // console.log(this.area, this.country);

      this.temperature = (responseData.main.temp - 273.15).toFixed(1) + '°C';
      this.temperature_feel =
        (responseData.main.feels_like - 273.15).toFixed(1) + '°C';
      this.humidity = responseData.main.humidity + '%';

      // console.log(this.temperature, this.temperature_feel, this.humidity);

      this.description = responseData.weather[0].description;
      this.weather_icon = `http://openweathermap.org/img/wn/${responseData.weather[0].icon}@2x.png`;

      this.windspeed = responseData.wind.speed + 'm/s';

      this.isFetch = false;

      this.firstName = this.$store.getters.firstname

      // console.log(new Date().toDateString());
    };

    getWeather();
  },
  computed: {
    greetingMes() {
      if (this.hour < 12) {
        return 'Good Morning';
      } else if (this.hour <= 18) {
        return 'Good Afternoon';
      } else {
        return 'Good Evening';
      }
    },
    icon_link() {
      if (this.hour < 4) {
        return true;
      } else if (this.hour < 18) {
        return false;
      } else {
        return true;
      }
    },
    isOutside() {
      if (
        this.description.replace(/\s*/g, '').toLowerCase().indexOf('rain') !==
          -1 ||
        this.description
          .replace(/\s*/g, '')
          .toLowerCase()
          .indexOf('extreme') !== -1 ||
        this.description.replace(/\s*/g, '').toLowerCase().indexOf('snow') !==
          -1
      ) {
        return false;
      } else {
        return true;
      }
    },
  },
  methods: {
    selectIndoor(option) {
      this.indoorHovered = option;
    },
  },
};
</script>

