<template>
  <teleport to="body">
    <div v-if="show" @click="quit" class="backdrop"></div>
    <transition name="dialog">

      <dialog open v-if="show">  
        <div class="position-relative">
          <img v-if="isLoading" src="../../assets/spinner_trans.gif" alt="spinner" class="position-absolute" style="top:45%;left: 40%;z-index: 100;"/>
          <header class="text-center">
          <img src="../../assets/boxing.png" alt="" />
          <h2 class="pt-3">Let's Get Started</h2>
          <div class="check" v-if="httpCheck">Wrong Username or Password</div>
        </header>
        <section>
          <div class="form-floating mb-4">
            <input
              type="text"
              class="form-control"
              :class="{ 'is-invalid': isUsernameNull }"
              id="floatingInput"
              placeholder="username"
              v-model="username"
            />
            <label for="floatingInput">Username</label>
            <div class="invalid-feedback">Please input the username.</div>
          </div>
          <div class="form-floating">
            <input
              type="password"
              class="form-control"
              :class="{ 'is-invalid': isPasswordNull }"
              id="floatingPassword"
              placeholder="Password"
              v-model="password"
            />
            <label for="floatingPassword">Password</label>
            <div class="invalid-feedback">Please input the password.</div>
          </div>
        </section>
        <div class="text-center">
          <div>
            <button
              type="button"
              class="btn btn-primary py-2 my-2"
              @click="login"
            >
              Login
            </button>
          </div>
          <div>
            <button
              type="button"
              class="btn btn-warning py-2 my-2"
              @click="quit"
            >
              Cancel
            </button>
          </div>

          <h3>or</h3>
          <button
            type="button"
            class="btn btn-success py-2 mt-2 mb-4"
            @click="
              () => {
                this.$emit('toggle', 'login');
              }
            "
          >
            Signup
          </button>
        </div>
        </div>
        
      </dialog>
    </transition>
  </teleport>
</template>

<script>
export default {
  props: {
    show: {
      type: Boolean,
      required: true,
    },
    title: {
      type: String,
      required: false,
    },
    fixed: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  emits: ['close'],
  data() {
    return {
      username: '',
      password: '',
      isUsernameNull: false,
      isPasswordNull: false,
      httpCheck: false,
      isLoading:false
    };
  },
  watch: {
    username(newVal) {
      if (newVal !== '') {
        this.isUsernameNull = false;
      }
    },
    password(newVal) {
      if (newVal !== '') {
        this.isPasswordNull = false;
      }
    },
  },
  computed: {},
  methods: {
    async login() {
      if (this.username === '' || this.password === '') {
        if (this.username === '') {
          this.isUsernameNull = true;
        }
        if (this.password === '') {
          this.isPasswordNull = true;
        }
        return;
      }

      
      this.isLoading = true;


      const loginData = {
        username: this.username,
        password: this.password,
      };



      // const response = await fetch('http://127.0.0.1:5000/test')

      const response = await fetch('https://my-aware2.onrender.com/login', {
        method: 'POST',
        body: JSON.stringify(loginData),
        headers: { 'content-type': 'application/json' },
      });

      if (!response.ok) {
        console.log('gege');
        return;
      }

      const responseData = await response.json();
      console.log(responseData);

      this.$store.commit('setUserId', responseData.user_id);
      this.$store.commit('setToken', responseData.token);
      this.$store.commit('setFirstname', responseData.first_name);
      this.$store.commit('setUsername', responseData.username);

      // 今天
      const response_record = await fetch('https://my-aware2.onrender.com/record/search', {
        method: 'POST',
        body: JSON.stringify({
          date:new Date().toDateString(),
          user_id:responseData.user_id
        }),
        headers: { 'content-type': 'application/json' },
      });

      const responseDateRecord = await response_record.json()

    
      this.$store.commit('setToday',responseDateRecord)

      // console.log(this.$store.getters.record_today);


      // 昨天
      const response_record1 = await fetch('https://my-aware2.onrender.com/record/search', {
        method: 'POST',
        body: JSON.stringify({
          date:new Date(new Date().getTime() - 24*60*60*1000).toDateString(),
          user_id:responseData.user_id
        }),
        headers: { 'content-type': 'application/json' },
      });

      console.log(new Date(new Date().getTime() - 24*60*60*1000).toDateString());

      const responseDateRecord1 = await response_record1.json()

      

    
      this.$store.commit('set1',responseDateRecord1)

      // console.log(this.$store.getters.record1);

      const response_record2 = await fetch('https://my-aware2.onrender.com/record/search', {
        method: 'POST',
        body: JSON.stringify({
          date:new Date(new Date().getTime() - 24*60*60*1000*2).toDateString(),
          user_id:responseData.user_id
        }),
        headers: { 'content-type': 'application/json' },
      });

      const responseDateRecord2 = await response_record2.json()

    
      this.$store.commit('set2',responseDateRecord2)

      const response_record3 = await fetch('https://my-aware2.onrender.com/record/search', {
        method: 'POST',
        body: JSON.stringify({
          date:new Date(new Date().getTime() - 24*60*60*1000*3).toDateString(),
          user_id:responseData.user_id
        }),
        headers: { 'content-type': 'application/json' },
      });

      const responseDateRecord3 = await response_record3.json()

    
      this.$store.commit('set3',responseDateRecord3)

      this.isLoading = false;

      this.$router.push('/home/inforecom');
    },
    quit() {
      this.$emit('quit', 'login');
    },
  },
};
</script>

