  <Signup :show="isSignup" @quit="backFrom" @toggle="toggleFrom"></Signup>
  <section class="showcase" :class="{ active: isActive }">
    <header>
      <div class="logo">
        <img src="../assets/logo.png" />
        <h3>Aware</h3>
      </div>
      <div class="toggle-text">
        <div class="d-flex">
          <router-link class="px-3" to="/"> <p>Home</p> </router-link>
          <p class="px-3" @click="()=>{isLogin = !isLogin}">Login</p>
          <p class="px-3" @click="()=>{isSignup = !isSignup}">Signup</p>
        </div>
      </div>
      <div
        class="toggle"
        :class="{ active: isActive }"
        @click="toggleActive"
      ></div>
