    <ul>
      <li><a href="/">Home</a></li>
      <li><a href="#" @click="()=>{isLogin = !isLogin}">Login</a></li>
      <li><a href="#" @click="()=>{isSignup = !isSignup}">Signup</a></li>
    </ul>
  </div>

  <div class="content">
    <h3>Aware focus on...</h3>
