  </div>

  <ProjectFooter></ProjectFooter>
</template>

<script>
// @ is an alias to /src

import ProjectFooter from '@/components/UI/ProjectFooter.vue';
import Signup from '@/components/User/SignUp.vue';
import LogIn from '@/components/User/LogIn.vue';


export default {
  name: 'HomeView',
  components: { ProjectFooter, LogIn, Signup },
  emits: ['quit'],
  data() {
    return {
      isActive: false,
      isLogin: false,
      isSignup:false
    };
  },
  methods: {
    toggleActive() {
      this.isActive = !this.isActive;
    },
    backFrom(resource) {
      if (resource === 'login') {
        this.isLogin = false;
      }
      if (resource === 'signup') {
        this.isSignup = false;
      }
    },
    toggleFrom(resource){
      if (resource === 'login') {
        this.isLogin = false;
        this.isSignup = true;
      }
      if (resource === 'signup') {
        this.isSignup = false;
        this.isLogin = true;
      }
    }
  },
};
</script>

